<template>
  <div>
    <div class="box">
      <div class="box1" @click="onshow"></div>
      <van-popup v-model="isShow" position="right" :style="{ height: '100%', width:'80%'}" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isShow: false
    };
  },
  methods: {
    onshow() {
      this.isShow = true;
    }
  }
};
</script>

<style lang="less">
.box {
  width: 100%;
  height: 100%;
  background: #fff;
  .box1 {
    width: 50px;
    height: 50px;
    background-color: red;
  }
}
</style>