<template>
  <div style="width:100%;height:100%">
    <!--地图容器-->
    <div id="baidu_map" style="width:100%;height:100%"></div>
    <transition name="draw">
      <!--这里的name 和 css 类名第一个字段要一样-->
      <div class="popups" v-show="boxshow"></div>
    </transition>
  </div>
</template>
<script>
import {  } from "../../api/map.js";
export default {
  name: "",
  data() {
    return {
      boxshow: false
    };
  },

  mounted() {
    this.$store.commit("showLoading");
    // 百度地图API功能
    // 创建Map实例

    let BMap = window.BMap;
    // 创建百度地图对象
    this.map = new BMap.Map("baidu_map");

    // 给地图绑定一个手指滑动时间,滑动时让列表隐藏
    this.map.addEventListener("movestart", () => {
      this.boxshow = false;
    });

    // 创建地址解析器对象
    var myGeo = new BMap.Geocoder();

    // 定义一个缩放级别,绑定在this上
    /* 
      level:11  第一级  表示区的缩放级别
      level:13  第二级  表示镇的缩放级别
      level:15  第三级  表示小区的缩放级别
     */
    this.level = 11;

    // 逆地址转换
    myGeo.getPoint(
      "深圳市",
      point => {
        if (point) {
          // 添加地图标记
          // this.map.addOverlay(new BMap.Marker(point));
          // 添加比例尺
          // this.map.addControl(new BMap.ScaleControl());
          // 添加缩放
          this.map.addControl(new BMap.NavigationControl());
          // 添加文字标记方法
          this.addOverlay("AREA|a6649a11-be98-b150", point, this.level);
        }
      },
      "广东省"
    );
  },
  methods: {
    // 添加文字标记方法
    // id是地区id ,point指定文本标注所在的地理位置
    async addOverlay(id, point, level) {
      if (level) {
        this.level = 11;
      } else {
        if (this.level === 11) {
          this.level = 13;
        } else if (this.level === 13) {
          this.level = 15;
        }
      }
      this.$store.commit("showLoading");
      let res = await (id);
      // 循环返回的数据
      res.data.body.map(item => {
        // 根据返回数据,获取经纬度
        let { longitude, latitude } = item.coord;
        // 根据经纬度生成一个百度坐标点
        let pos = new BMap.Point(longitude, latitude);

        // 第一个参数是地图的经纬度,第二个参数是地图的缩放级别
        this.map.centerAndZoom(pos, this.level);

        let opts;
        let label;
        if (this.level !== 15) {
          // 添加文字标签
          opts = {
            position: pos, // 指定文本标注所在的地理位置
            offset: new BMap.Size(-25, -25) //设置文本偏移量
          };
          // 声明覆盖物
          label = new BMap.Label(
            "<div class='map-tag'>" +
              item.label +
              "<br>" +
              item.count +
              "套</div>",
            opts
          ); // 创建文本标注对象
          label.setStyle({
            border: 0,
            background: "transparent"
          });
          // 点击地图标注,再次调用添加标注方法
          label.addEventListener("click", () => {
            this.refreshMap(item.value, pos);
          });
          // 把覆盖物添加到地图上
          this.map.addOverlay(label);
        } else {
          // 添加文字标签
          opts = {
            position: pos, // 指定文本标注所在的地理位置
            offset: new BMap.Size(-25, -25) //设置文本偏移量
          };
          // 声明覆盖物
          label = new BMap.Label(
            "<div class='map-tag1'>" +
              item.label +
              "&nbsp;&nbsp;&nbsp;" +
              item.count +
              "套</div>",
            opts
          ); // 创建文本标注对象
          label.setStyle({
            border: 0,
            background: "transparent"
          });
          // 点击地图标注,再次调用添加标注方法
          label.addEventListener("click", e => {
            if (this.boxshow) {
              this.boxshow = false;
            }
            console.log(e);
            this.boxshow = true;
          });
          // 把覆盖物添加到地图上
          this.$store.commit("hideLoading");
          this.map.addOverlay(label);
        }

        this.$store.commit("hideLoading");
      });
    },

    // 清除地图上的覆盖物
    refreshMap(id, point) {
      // 清除地图覆盖物会报错,用定时器包裹一下,就可以解决
      setTimeout(() => {
        // 清除地图上的覆盖物
        this.map.clearOverlays();
        // 重新生成地图上的覆盖物
        this.addOverlay(id, point);
      }, 0);
    }
  }
};
</script>
<style lang="less">
html {
  height: 100%;
}
body {
  height: 100%;
  margin: 0px;
  padding: 0px;
  .map-tag {
    background-color: #eb4450;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    line-height: 50px;
    text-align: center;
    color: aliceblue;
    line-height: 20px;
  }
  .map-tag1 {
    width: 126px;
    height: 20px;
    // border: 2px solid red;
    background: #fff;
    position: relative;
    border-radius: 3px;
    /*   &:before {
      content: "";
      width: 0;
      height: 0;
      border: 3px solid;
      border-color: red transparent transparent;
      position: absolute;
      left: 30px;
      bottom: -8px;
    } */
    &::after {
      content: "";
      width: 0;
      height: 0;
      border: 3px solid;
      border-color: #fff transparent transparent;
      position: absolute;
      left: 30px;
      bottom: -6px;
    }
  }
  // 弹出层
  .popups {
    position: absolute;
    left: 0;
    bottom: 0;
    height: 50%;
    width: 100%;
    background-color: #ffffff;
  }
  .draw-enter-active,
  .draw-leave-active {
    transition: all 1s ease;
  }
  .draw-enter,
  .draw-leave-to {
    height: 0;
  }
}
</style>