<template>
  <div>
    <verifyhead>
      <div slot="p1">
        <p>请输入</p>
        <p>您的手机账号</p>
      </div>
    </verifyhead>
    <!--手机和验证码 -->
    <div class="verify-body">
      <van-field v-model="userphone" center @blur="losefocus" maxlength="11">
        <div slot="label" class="theno">+86</div>
      </van-field>
      <van-field
        v-model="usercode"
        center
        placeholder="请输入验证码"
        @blur="verCode"
        class="getcode"
        maxlength="6"
      >
        <van-button slot="button" size="mini" color="#C30D1EFF" plain @click="getcode">获取验证码</van-button>
      </van-field>
      <!-- 下一步 -->
      <div class="next-button">
        <van-button size="large" color="#C30D1EFF" @click="$router.push({path:'roles'})">下一步</van-button>
        <van-button size="large" color="#C30D1EFF" plain>取消</van-button>
      </div>
    </div>
  </div>
</template>

<script>
import verifyhead from "../components/verifyhead";
import { verify } from "crypto";
export default {
  name: "phoneVerify",
  components: { verifyhead },
  data() {
    return {
      userphone: "",
      usercode: ""
    };
  },
  methods: {
    getcode() {
      this.verify();
    },
    losefocus() {
      this.verify();
    },
    // 手机校验
    verify() {
      var pattern = /0?(13|14|15|18|17)[0-9]{9}/;
      var str = this.userphone;
      if (this.userphone == "") {
        this.$toast("手机号不能为空");
      } else if (!pattern.test(str)) {
        this.$toast("请输入正确的手机号");
      }
    },
    verCode() {
      let reg = /^\d{6}$/;
      let num = this.usercode;
      if (!reg.test(num)) {
        this.$toast("请输入6位验证码");
      }
    }
  }
};
</script>

<style lang="less" scopde>
p {
  font-weight: 500;
}
.verify-body {
  padding: 60px 30px 0;
  .van-field__label {
    width: 52px;
  }
  .theno {
    width: 52px;
    height: 29px;
    line-height: 29px;
    background-color: #d8d8d8ff;
    font-size: 20px;
    text-align: center;
    margin-left: 28px;
  }
  .van-cell--center {
    padding: 0;
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #787878ff;
    margin-bottom: 21px;
    &:nth-child(2) {
      margin-bottom: 0;
    }
    input {
      margin-left: 15px;
      width: 163px;
      height: 100%;
      font-size: 23px;
      font-weight: 600;
      color: #000000ff;
      margin-left: 35px;
    }
    .van-button {
      margin-left: 26px;
      font-size: 14px;
      width: 81px;
      height: 25px;
      border-radius: 13px;
      line-height: 25px;
      text-align: center;
    }
  }
  .getcode {
    input {
      font-weight: 400;
      font-size: 18px;
      font-weight: 400;

      color: #787878ff;
      &::-webkit-input-placeholder {
        /* WebKit browsers */
        color: #787878ff;
        font-size: 18px;
      }
      &:-moz-placeholder {
        /* Mozilla Firefox 4 to 18 */
        color: #787878ff;
        font-size: 18px;
      }
      &::-moz-placeholder {
        /* Mozilla Firefox 19+ */
        color: #787878ff;
        font-size: 18px;
      }
      &:-ms-input-placeholder {
        /* Internet Explorer 10+ */
        color: #787878ff;
        font-size: 18px;
      }
    }
  }
  .next-button {
    .van-button {
      height: 44px;
      text-align: center;
      line-height: 44px;
      &:nth-child(1) {
        margin: 42px 0 16px;
      }
    }
  }
}
</style>