<template>
  <div>
    <verifyhead>
      <div slot="p1">
        <p>请选择</p>
        <p>您的身份角色</p>
      </div>
    </verifyhead>
    <div class="roles-box">
      <van-radio-group v-model="radio">
        <van-cell-group :border="false">
          <van-cell
            clickable
            :border="false"
            v-for="(item,index) in idList"
            :key="index"
            @click="cellSelect(index)"
          >
            <div slot="title" class="roles-box-l">
              <div>
                <img :src="item.id_img" alt />
              </div>
              <span>{{item.id_name}}</span>
            </div>
            <van-radio slot="right-icon" :name="item.name" checked-color="#C30D1EFF" />
          </van-cell>
        </van-cell-group>
      </van-radio-group>
      <div class="next-btn">
        <van-button color="#C30D1EFF" @click="identityDialog">下一步</van-button>
        <van-button plain color="#C30D1EFF" @click="$router.back(-1)">取消</van-button>
      </div>
    </div>

    <!-- 弹窗 -->
    <van-dialog v-model="riskShow" :show-confirm-button="false" class="risk-dialog">
      <h2>温馨提示</h2>
      <div class="dialog-con">
        <p>
          您已经选择了
          <span>{{role}}</span>角色,通过审核后将不能再次更改,请慎重考虑!
        </p>
      </div>
      <div class="next-button">
        <van-button color="#C8C8C8FF" round @click="riskShow=false">取消</van-button>
        <van-button v-if="disabled" color="#64D857FF" :disabled="disabled" round>确认({{s}})</van-button>
        <van-button v-else color="#64D857FF" :disabled="disabled" round @click="riskShow=false">确认</van-button>
      </div>
    </van-dialog>
  </div>
</template>

<script>
import verifyhead from "../components/verifyhead";
export default {
  name: "roles",
  components: { verifyhead },
  data() {
    return {
      radio: 1,
      idList: [
        {
          name: 1,
          id_name: "市场商户",
          id_img: require("../../../assets/img/mine/mange/市场商户@2x.png")
        },
        {
          name: 2,
          id_name: "运营商",
          id_img: require("../../../assets/img/mine/mange/运营商@2x.png")
        },
        {
          name: 3,
          id_name: "外场商户",
          id_img: require("../../../assets/img/mine/mange/外场商户@2x.png")
        },
        {
          name: 4,
          id_name: "周边商家",
          id_img: require("../../../assets/img/mine/mange/周边商家@2x.png")
        }
      ],
      // 风险提示弹出框
      riskShow: false,
      // 秒表初始时间
      s: 5,
      // 计时按钮默认禁止
      disabled: true,
      // 入住管理弹出框
      enterShow: false,
      // 倒计时定时器
      timeID: "",
      // 角色
      role: ""
    };
  },
  methods: {
    cellSelect(index) {
      let i = index + 1;
      this.radio = i;
    },
    // 身份确认弹窗
    identityDialog() {
      if (this.radio) {
        // 打开弹窗
        this.riskShow = true;
        // 确认按钮禁用
        this.disabled = true;
        // 弹窗点开后重置倒计时
        this.s = 5;
        // 清除之前的定时器
        clearInterval(this.timeID);
        // 开启计时器
        this.timeID = setInterval(() => {
          this.s--;
          if (this.s == 0) {
            this.disabled = false;
            this.s = 5;
          }
        }, 1000);
      }
      if (this.radio == 1) {
        this.role = "市场商户";
      } else if (this.radio == 2) {
        this.role = "运营商";
      } else if (this.radio == 3) {
        this.role = "外场商户";
      } else if (this.radio == 4) {
        this.role = "周边商家";
      }
    }
  }
};
</script>

<style lang="less" scoped>
p {
  font-weight: 500;
}
.roles-box {
  padding: 19px 43px;
  .van-cell--clickable {
    padding: 0;
    width: 290px;
    height: 80px;
    margin-bottom: 12px;
    box-shadow: -0.5px 0.5px 1px 1px #cdcdcd80;
    .roles-box-l {
      display: flex;
      align-items: center;
      height: 100%;
      img {
        width: 66px;
        margin-left: 23px;
        margin-right: 40px;
      }
      span {
        font-size: 15px;
        color: #000000ff;
        font-weight: 700;
      }
    }
    /deep/.van-icon {
      border: 1px solid #c30d1eff;
      margin-right: 25px;
      font-size: 24px;
      text-align: center;
    }
    /deep/ .van-icon::before {
      margin-top: 5px;
    }
  }
  // 下一步按钮样式
}
.next-btn {
  padding-top: 12px;
  /deep/.van-button {
    width: 100%;
    height: 44px;
    font-size: 15px;
    &:nth-child(1) {
      margin-bottom: 16px;
    }
  }
}
/* 弹窗样式 */
.risk-dialog {
  padding: 15px 25px;
  box-sizing: border-box;

  .dialog-con {
    width: 100%;
    height: 180px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    p {
      margin-top: 31px;
      color: #787878ff;
      font-size: 13px;
      line-height: 20px;
      text-align: left;
      margin-bottom: 55px;
      span {
        font-size: 16px;
        color: #c30d1eff;
      }
    }
  }
  .next-button {
    display: flex;
    justify-content: space-between;
    .van-button {
      height: 35px;
      line-height: 35px;
      text-align: center;
      width: 115px;
    }
  }
}
</style>