          name: "分享",
          num: "1182"
        }
      ],

      // 风险提示弹出框
      riskShow: false,
      // 秒表初始时间
      s: 0,
      // 计时按钮默认禁止
      disabled: true,
      // 入住管理弹出框
      enterShow: false,
      // 倒计时定时器
      timeID: ""
    };
  },
  methods: {
    enterPop() {
      this.enterShow = true;
      this.count(5);
    },
    emptyEnter() {
      this.riskShow = true;
      this.count(5);
    },
    count(num) {
      clearInterval(this.timeID);
      this.disabled = true;
      this.s = num;
      this.timeID = setInterval(() => {
        this.s--;
        if (this.s == 0) {
          this.disabled = false;
        }
      }, 1000);
    },
    // 我的历史点击事件
    toHistoryPage(i) {
      console.log(i);
      if (i == 0) {
        this.$router.push({ path: "browse" });
      } else if (i == 1) {
        this.$router.push({ path: "attention" });
      } else if (i == 2) {
      } else if (i == 3) {
      }
    }
  }
};
</script>

